@import url(https://fonts.googleapis.com/css2?family=Alegreya&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{ 
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Alegreya', serif;
    

} 

html{ 
  background: url(/Portfolio/static/media/leone-venter-VieM9BdZKFo-unsplash.e591a57d.jpg) no-repeat center center fixed;
  background-size: cover ;
  position: relative;

}

body{
  min-height: 100vh;
  color: rgb(2, 40, 73);
}

h1{
  text-align: center;
  padding: 0.5rem;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  height: 8vh;
  color: rgb(2, 40, 73);;
  /* box-shadow: 0 20px 20px rgba(114, 253, 54, 0.5); */
  box-shadow: 0 20px 20px rgba(220, 20, 60, 0.5);
  transform: 10px;
}



.header{
  color: red;
  font-size: 40px;
  
}
.App #head-text{
  color: red;
  font-size: 40px;
  padding: 10px;
}

.navbar{
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: rgb(0, 2, 3);
  list-style: none;
  font-size: large;
  font-weight: bolder;
  padding: 15px; 
  height: 10vh;
  opacity: 0.8;
}

.navbar li a{
  text-decoration: none;
}

.nav-home a{
  color: rgb(0, 2, 3);
  cursor: pointer;
  width: 10px;
  padding: 10px;
}

.nav-home a:hover{
  background-color: rgb(255, 200, 117);
  border-radius: 50px;
}
button{
  cursor: pointer;
}

.nav-links{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  cursor: pointer;

}
.nav-links a{
  color: rgb(0, 2, 3);
  cursor: pointer;
  width: auto;
  padding: 10px;
}


 a:hover{
  background-color: rgb(255, 200, 117);
  border-radius: 50px;
}


.banner{
  display: flex;
  margin-left: 25%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  width: 50%;
  opacity: 0.9;
}

.card {
  /* min-height: 80vh; */
  width: 35rem;
  box-shadow: 0 20px 20px rgba(245, 130, 130, 0.3), 0px 0px 50px rgba(243, 120, 120, 0.3);
  border-radius: 30px;
  padding: 0rem 5rem;
  background-color: lightblue;
}
@media (max-width: 800px) {
  .card  {
    flex-direction: row;
    width: 400px;
  }
}
.card-image{
  margin-top: 80px;
  min-height: 34vh;
  display: flex;
  align-items: center;
  justify-content: center;
 }

.card-image img{
  width: 12rem;
  z-index: 2;
  border-radius: 50%;
}
@media (max-width: 800px) {
  .card-image img{
    flex-direction: row;
    width: 300px;
  }
}
.circle{
  width: 18rem;
  height: 18rem;
  background: linear-gradient(
    to right,
    rgba(245,70,66,0.75),
    rgba(8,83,156,0.75)
  );
  padding: 20px;
  position: absolute;
  border-radius: 50%;
  z-index: 1;
}

.card-text{
  margin-top: 30px;
  min-height: 35vh;
  padding: 35px;
  font-size: 1rem;
  padding: 2rem 0rem;
  font-weight: lighter;
  color: rgba(0, 0, 0, 0.986);
  text-align: justify;
}



.card-text #sub-text{
  color: red;
}

.card-para h2{
  font-size: large;
}

.container-about{
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin: auto;
  width: auto; 
  height: auto;
  font-size: large;
  font-weight: normal;
  text-align: justify;
  position: relative;
  /* padding: 20px; */
}
.download{
  text-decoration: none;
  margin-left: 20%;
  margin-right:20% ;
  padding: 10px;
}
.download button{
  padding: 3px;
  box-shadow: 0 2px 2px red;
  position: absolute;
}
.download button:hover{
  color: red;
  font-size: 22px;
  background: lightblue;
  
}
@media (max-width: 800px) {
  .container-about{
    flex-direction: column;
    padding: 5px;
  }
  .container-about #para{
    width: 100%;
    padding: 5px;
    line-height: 1.5;
  }
  .download{
    text-decoration: none;
    margin-left: 20%;
    margin-right:20% ;
  }
  .download button:hover{
    color: red;
    font-size: 22px;
    background: lightblue;
    box-shadow: 0 2px 2px red;
  }
}

#header{
  margin: auto;
  color: red;
  padding: 10px;
  
}

.container-about-avathar img{
  width: 500px;
  height: 400px;
  border-radius: 50%;
  padding: 20px;
}
@media (max-width: 800px) {
  .container-about-avathar img{
    flex-direction: row;
    width: 300px;
    height: 300px;
    margin-left: 8px;
    border: 5px solid red;

  }
  .container-about-info{
    width: 300px;
    margin-left: 15px ;
  }
}
.container-about-info{
  padding:0 2rem;
}

 #para p{
  font-size: 22px;
  line-height: 30px;
  text-indent: 2px;
  color: rgb(2, 40, 73);}

.footer-text p{
 
  font-size: 22px;
  line-height: 30px;
  text-indent: 2px;
  color: rgb(2, 40, 73);}

.container-skills{
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  margin-left: 10%;
  margin-right: 10%;
  height: 50px;
}
@media (max-width: 800px) {
  .container-skills{
    flex-direction: row;
  }
  .skill-image img{
    width: 80px;
    height: 10vh;
    border-radius: 15px;
  }
}
.skill-image{
  border-radius: 8px;
  width: 33%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  line-height: 25px;
  margin: 60px aut;
}

.skill-image img{
  width: 100px;  
  height: 14vh;
  border-radius: 20px;
}

.skill-image li{
  margin: 20px 0;
  width: 100%;
  text-align: center;
}

.bar{
  background: rgb(101, 131, 92);
  display: block;
  height: 10px;
  border: 1px solid rgba(233, 98, 98,0.6);
  border-radius: 3px;
  overflow: hidden;
}
.bar span{
  height: 10px;
  float: left;
  background: blue;
  list-style:none;
}
.html{
  width: 90%;
  animation: html 2s;
}
.css{
  width: 80%;
  animation: css 2s;
}
.JavaScript{
  width: 70%;
  animation: JavaScript 2s;
}
.bootstarp{
  width: 60%;
  animation: bootstrap 2s;
}
.node{
  width: 70%;
  animation: node 2s;
}
.React{
  width: 55%;
  animation: React 2s;
}
.mongo{
  width: 70%;
  animation: mongo 2s;
}
.Git{
  width: 70%;
  animation: Git 2s;
}
.Java{
  width: 65%;
  animation: Java 2s;
}
@keyframes html{
  0% {
    width: 0%;
  }
  100%{
    width: 90%;
  }
}
@keyframes css{
  0% {
    width: 0%;
  }
  100%{
    width: 80%;
  }
}
@keyframes JavaScript{
  0% {
    width: 0%;
  }
  100%{
    width: 70%;
  }
}

@keyframes bootstrap{
  0% {
    width: 0%;
  }
  100%{
    width: 60%;
  }
}
@keyframes node{
  0% {
    width: 0%;
  }
  100%{
    width: 70%;
  }
}
@keyframes React{
  0% {
    width: 0%;
  }
  100%{
    width: 55%;
  }
}
@keyframes mongo{
  0% {
    width: 0%;
  }
  100%{
    width: 70%;
  }
}
@keyframes Git{
  0% {
    width: 0%;
  }
  100%{
    width: 70%;
  }
}
@keyframes Java{
  0% {
    width: 0%;
  }
  100%{
    width: 65%;
  }
}
.container-project{
 display: flex;
 flex-direction: row;
 justify-content: center;
 align-items: top;
 padding: 0 2rem;
 height: auto;
 width: 100%;
 background: transparent;

}
@media (max-width: 800px) {
  .container-project{
