}
.contact-info{
  font-family: 'Alegreya', serif;
  font-size: 22px;
  text-align: center;
  line-height: 2;
  color: rgb(231, 220, 170);;

}
@media (max-width: 800px) {
  .container-contact{
    flex-direction: column;
    justify-content: center;
